$bold: 400;
$bolder: 500;
$boldest: 600;
$blue: #707EFD;
$gray: rgb(221,221,221);
$darkgray: rgb(100, 100, 100);

#logo-container{
  cursor: pointer;
}

#search-btn {
  background-color: white;
  border: 1px solid black;
  cursor: pointer;
  height: 100%;
}

button{
  background-color: white;
  border: 1px solid black;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

button:hover,
#search-btn:hover{
  background-color: $blue;
  color: white;
  border: 1px solid transparent;
}

input[type=text]{
  border: 1px solid black;
  padding: 0 5px;
}

input[type=text]:focus{
  outline: none;
}

footer > ul{
  display: flex;
  justify-content: space-around;
}

.main{
  display: flex;
  flex-direction: column;
}

.search{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-image: url('../League-of-Legos/src/assets/background.jpg');
  background-size: 100% 100%;
  height: 50vh;
  position: relative;
  margin-top: 46px;
}

.search-2{
  display: flex;
  justify-content: center;
  align-items: center;
}

.search > div:first-child{
  display: flex;
}

.search > div > * {
  margin: 0 3px;
  border-radius: 10px;
}

.game-info {
  padding: 10px;
  display: flex;
  flex-direction: column;
  position:relative;
  min-width: 500px;
}

.gold-graphs {
  display: flex;
  justify-content: space-around;
  margin-top: 15px;
}

.players-container {
  color: rgb(135, 135, 255);
}

.red{
  color: rgb(255, 122, 122);
}

body * {
  font-family: 'Montserrat', sans-serif;
}

h1{
  font-weight: $boldest;
  font-size: 40px;
}

.playerinfotable {
  width: 100%;
  background-color: $darkgray;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

th {
  color: white;
  border: 1px white solid;
}

td{
  padding: 3px;
  border: 1px white solid;
}

tr {
  // border: 1px solid yellow;
  font-weight: $bolder;
}

.footer-img {
  height: 30px;
  width: auto;
  color: white;
}

#team1, #team2 {
  height: 30px;
  width: 150px;
  font-size: 25px;
}

a {
  text-decoration: none;
  color: inherit;
}

h2 {
  font-size: 32px;
  font-weight: $bolder;
}

.game-info > p:first-child {
  position: absolute;
  font-weight: bolder;
  font-size: 15px;
  top: 0;
  left: 5px;
}

.top-middle {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%
}

.hidden {
  display: none !important;
}

#league-img {
  width: 100%;
  height:100%;
}

#logo{
  height: 30px;
}

.winner {
  position: absolute;
  top: 0;
  left: 45%;
  width: 10%;
  line-height: 15px;
}

.switch-graphs{
  position: absolute;
  right: 15px;
  top: 0;
}

.winner, .switch-graphs{
  border-radius: 5px;
}

ul{
  display:flex;
}

#top-nav{
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  height: 30px;
  width: 80vw;
  top: 0;
  z-index: 2;
  background-color: $blue;
  color:white;
  padding: 8px 10vw;
}

#logo ~ p {
  font-weight: $bold;
  font-size: 25px;
}

#top-nav > div:first-child{
  display: flex;
  align-items: center;
  justify-content: center;
}

ul > li {
  padding: 5px;
}

.pie-chart:hover{
  cursor: pointer;
}

.table-row:hover{
  border: 2px solid !important;
}

#modal-container{
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 5;
}

#modal-container > #modal-background {
  height: 100vh;
}

#modal-container > .modal-text {
  transition: all .5s ease;
}

.modal-background{
  width: 100vw;
  height: 100vh;
  background-color: rgb(144,144,144);
  opacity: .5;
  position: absolute;
}

#me{
  width: 100px;
}

.modal-text{
  z-index: 1;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: $gray;
  position: relative;
  width: 50vw;
  border-radius: 15px;
  color: black;
  min-width: 350px;
}

.modal-text > * {
  padding: 10px;
}

.initial{
  margin: 10px;
}

#modal-container-initial{
  display: flex;
  align-items: center;
  justify-content: center;
}

.personal-link:hover{
  transform: scale(1.1);
  transition-duration: 1s;
}

.modal-text > h1:first-child{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $blue;
  border-top-left-radius: inherit;
  border-top-right-radius: inherit;
  color: $gray;
}

#close-modal{
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 2em;
  padding: 5px;
}

#close-modal:hover {
  cursor: pointer;
}

.teams-list-container {
  display: flex;
}

.non-text{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.non-text > * {
  padding: 5px 0;
}

.non-text > h2 {
  border-bottom: 1px solid black;
}

.non-text > div > div {
  margin: 5px;
}

.non-text > p {
  font-size: 30px;
}

.non-text > div {
  display: flex;
  align-items: center;
}

.teams-list{
  display: flex;
  flex-direction: column;
  height: 20vh;
  width: 20vw;
  overflow-y: scroll;
  color: black;
}

.teams-list > li {
  padding: 5px;
}

.teams-list > li:hover {
  cursor: pointer;
}

.most-games{
  background-color: rgb(130, 255, 130);
}

.most-games:hover{
  background-color: rgb(0, 255, 0);
}

.average-games{
  background-color: rgb(255, 255, 150);
}

.average-games:hover{
  background-color: rgb(255, 255, 0);
}

.below-average-games{
  background-color: rgb(255, 200, 100);
}

.below-average-games:hover{
  background-color: rgb(255, 166, 0);
}

.teams-list::-webkit-scrollbar {
    width: 16px;
}
 
.teams-list::-webkit-scrollbar-track {
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 100px;
}
 
.teams-list::-webkit-scrollbar-thumb {
    background-color: $blue;
    border-radius: 100px;
}

#key > * {
  display: flex;
  align-items: center;
  padding: 5px 0;
}

.color-box{
  height: 20px;
  width: 20px;
}

#green{
  background-color: rgb(130, 255, 130);
}

#yellow{
  background-color: rgb(255, 255, 150);
}

#orange{
  background-color: rgb(255, 200, 100);
}

#sr{
  width: 75%;
}

#help {
  padding: 5px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid transparent;
}

#help:hover{
  background-color: white;
  border: 1px solid black;
  cursor: pointer;
  transform: scale(1.05);
  transition-duration: .1s;
}

#main {
  padding: 0 10vw;
}

#about-me{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.not-top{
  background-color: $gray !important;
  color: black !important;
}

b{
  font-weight: bolder;
}

#clipboard-message{
  position: fixed;
  top: 70px;
  z-index: 100;
  padding: 10px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  transition-duration: .5s;
  left: 50%;
  transform: translate(-50%, -50%);
  visibility: hidden;
  opacity: 0;
  transition: all .5s ease;
}

.visible{
  visibility: visible !important;
  opacity: 1 !important;
}

@media (max-width: 1200px) {
  .gold-graphs{
    flex-wrap: wrap;
  }
}